import React, { useState, useRef } from 'react';
import classnames from 'classnames';
import ButtonIcon from '../../button-icon';
import Chip from '../../chip';
import ChipContainer from '../../chip-container';
import ChipText from '../../chip-text';
import Icon from '../../icon';
import './input-chips-example.scss';

const KEY_CODES = {
  ARROW_DOWN: 40,
  ARROW_LEFT: 37,
  ARROW_RIGHT: 39,
  ARROW_UP: 38,
  DELETE: 8,
  END: 35,
  ENTER: 13,
  HOME: 36
};

const InputChipsExample = () => {
  const ref = useRef();
  const inputRef = useRef();
  const [value, setValue] = useState('');
  const [focusedId, setFocusedId] = useState(-1);
  const [focused, setFocused] = useState(false);
  const [state, setState] = useState({
    items: [
      {
        focused: false,
        id: 'input-chip-group-test-0',
        value: 'Mango'
      },
      {
        focused: false,
        id: 'input-chip-group-test-1',
        value: 'Orange'
      },
      {
        focused: false,
        id: 'input-chip-group-test-2',
        value: 'Apple'
      },
      {
        focused: false,
        id: 'input-chip-group-test-3',
        value: 'Peach'
      }
    ]
  });

  const handleInputClick = e => {
    setFocusedId(-1);
    setState({
      items: [
        ...state.items.map((item, i) => ({
          ...item,
          focused: false
        }))
      ]
    });
  };

  const handleContainerClick = e => {
    if (inputRef && inputRef.current) {
      inputRef.current.focus();
    }
  };

  const handleDelete = (e, id) => {
    const eventType = e.type;
    const isKeydown = e.type === 'keydown';
    const isClick = eventType === 'click';
    const isDeleteKey = isKeydown && (e.charCode === KEY_CODES.DELETE || e.keyCode === KEY_CODES.DELETE);
    const isLeftKey = isKeydown && (e.charCode === KEY_CODES.ARROW_LEFT || e.keyCode === KEY_CODES.ARROW_LEFT);
    const isRightKey = isKeydown && (e.charCode === KEY_CODES.ARROW_RIGHT || e.keyCode === KEY_CODES.ARROW_RIGHT);
    const items = state.items.map((item, i) => ({
      ...item,
      focused: isClick ? false : i === id - 1
    }));

    if (isClick || isDeleteKey) {
      if (id > -1 && items.length) {
        setState({
          items: [
            ...items.slice(0, id),
            ...items.slice(id + 1)
          ]
        });
        setFocusedId(isClick ? -1 : id - 1);
        if (ref && ref.current) {
          const nextEl = ref.current.querySelector(`#input-chip-group-test-${id - 1}`);
          if (nextEl) {
            nextEl.focus();
          }
        }
      }
    }

    if (isLeftKey) {
      setFocusedId(id > 0 ? (id % state.items.length) - 1 : state.items.length - 1);
      if (ref && ref.current) {
        const nextEl = ref.current.querySelector(`#input-chip-group-test-${id > 0 ? (id % state.items.length) - 1 : state.items.length - 1}`);
        if (nextEl) {
          nextEl.focus();
        }
      }
    }

    if (isRightKey) {
      setFocusedId(id < state.items.length - 1 ? (id % state.items.length) + 1 : 0);
        if (ref && ref.current) {
          const nextEl = ref.current.querySelector(`#input-chip-group-test-${id < state.items.length - 1 ? (id % state.items.length) + 1 : 0}`);
          if (nextEl) {
            nextEl.focus();
          }
        }
    }
  }

  const handleFocus = e => {
    setFocused(true);
  };

  const handleBlur = e => {
    setFocused(false);
  }

  const handleChange = e => {
    setFocusedId(-1);
    setState({
      items: [
        ...state.items.map((item, i) => ({
          ...item,
          focused: false
        }))
      ]
    });
    setValue(e.target.value);
  };

  const handleKeyDown = (e) => {
    const code = e.charCode || e.keyCode;
    const value = e.target.value;

    switch (code) {
      case KEY_CODES.DELETE: {
        if (focusedId === -1 && value === '' && state.items.length) {
          setFocusedId(state.items.length - 1);
          setState({
            items: [
              ...state.items.map((item, i) => ({
                ...item,
                focused: i === state.items.length - 1
              })),
            ]
          });
        }

        if (focusedId !== -1 && value === '' && state.items.length) {
          handleDelete(e, focusedId);
        }
        break;
      }

      case KEY_CODES.ENTER: {
        if (value !== '') {
          setState({
            items: [
              ...state.items,
              {
                focused: false,
                id: `input-chip-group-test-${state.items.length}`,
                value
              }
            ]
          });
          setValue('');
        }
        break;
      }

      case KEY_CODES.ARROW_LEFT: {
        if (value === '' && state.items.length) {
          const idToFocus = focusedId > 0 ? (focusedId % state.items.length) - 1 : state.items.length - 1;
          setFocusedId(idToFocus);
          setState({
            items: [
              ...state.items.map((item, i) => ({
                ...item,
                focused: i === idToFocus
              })),
            ]
          });
          setValue('');
        }
        break;
      }

      case KEY_CODES.ARROW_RIGHT: {
        if (value === '' && state.items.length) {
          const idToFocus = focusedId < state.items.length - 1 ? (focusedId % state.items.length) + 1 : 0;
          setFocusedId(idToFocus);
          setState({
            items: [
              ...state.items.map((item, i) => ({
                ...item,
                focused: i === idToFocus
              })),
            ]
          });
          setValue('');
        }
        break;
      }

      default:
        break;
    }
  };

  return (
    <ChipContainer
      className={classnames('vds-chip-type-input', {
        'vds-state--focus': focused
      })}
      onClick={handleContainerClick}
      ref={ref}
    >
        {
          state.items.map((item, i) => (
            <Chip key={i} focused={item.focused}>
              <ChipText>{item.value}</ChipText>
              <ButtonIcon
                ariaLabel={`delete ${item.value}`}
                className={classnames({ 'focus-visible': item.focused })}
                iconType="light-tiny"
                id={`input-chip-group-test-${i}`}
                onClick={e => handleDelete(e, i)}
                onKeyDown={e => handleDelete(e, i)}
                tabIndex="-1"
              >
                <Icon name="close" resolution="tiny" />
              </ButtonIcon>
            </Chip>
          ))
        }
        <input
          className="vds-chip-input"
          onBlur={handleBlur}
          onChange={handleChange}
          onClick={handleInputClick}
          onFocus={handleFocus}
          onKeyDown={handleKeyDown}
          ref={inputRef}
          type="text"
          value={value}
        />
    </ChipContainer>
  );
};

export default InputChipsExample;
